@mixin square($size) {
  width: $size;
  height: $size;
}

@mixin madeIcon($size) {
  display: block;
  @include square($size);

  background-repeat: no-repeat;
  background-position: center;
}

@mixin squareButton($size) {
  @include square($size);
  border: 1px solid $icons;
  display: flex;
  justify-content: center;
  align-items: center;

  & svg {
    fill: $primary;
  }

  &:hover {
    border-color: $primary;
  }

  &:disabled {
    border-color: $icons;

    &:hover {
      border-color: $icons;
    }

    & svg {
      fill: $icons;
    }
  }
}

@mixin primaryButton() {
  @extend %buttons;
  width: 100%;
  height: 40px;
  background-color: $primary;
  color: $white;
  line-height: 21px;

  &:hover {
    box-shadow: 0 3px 13px 0 rgba(23, 32, 49, 0.26);
  }

  &--selected {
    border: 1px solid $elements;
    color: $green;
  }
}

@mixin dropDown() {
  @extend %buttons;
  height: 40px;
  padding-left: 10px;
  border-color: $icons;

  appearance: none;
  background-image: url("../img/icons/Chevron\ \(Arrow\ Down\).svg");
  background-repeat: no-repeat;
  background-position: right 0.7rem top 50%;
  background-size: 16px;
  color: $primary;

  &:hover {
    border-color: $secondary;
  }
}
